import React from "react";
import { Parallax } from "react-parallax";
import background from "../images/fondoContador.jpeg";
import CountdownTimer from "../components/CountdownTimer";

export const ContadorDos = () => {
    var fechaCasamiento = new Date('2023-03-11T20:30:00')
    return (
        <div style={{
            backgroundImage: `url(${background})`
            , backgroundPosition: 'center',
            backgroundSize: "cover",
            aspectRatio: '1/1'
        }} className="page flex h-100 items-center justify-end flex-1">
            <div className="w-100 flex flex-col text-center m-auto p-10">
                <span className="text-green text-2xl font-bold pb-4">
                    Falta menos!
                </span>
                <span className="text-green text-xl font-bold pb-4">
                    11 de Marzo de 2023
                </span>
                <div>
                    <CountdownTimer targetDate={fechaCasamiento} />
                </div>
                <span className="text-green pt-4 text-l font-bold pb-4">
                    Te envíamos más información y detalles a tener en cuenta
                </span>
            </div>
        </div >
    )
}
