import React, { Component } from "react";
import { InlineIcon } from '@iconify/react';
import Modal from 'react-bootstrap/Modal';
import { AwesomeButton, AwesomeButtonSocial } from "react-awesome-button";


class Confirmacion extends Component {
    mensajeWp = 'https://wa.me/59898926042?text=';
    constructor(props) {
        super(props);
        this.state = {
            celiaco: false,
            diabetico: false,
            vegano: false,
            nombre: '',
            showModal: false,
            showModalTexto: false,
            showErrorModal: false,
            invitados: [],
            invitadosConfirmados: {},
            showErrorMessage: false,
            mostrarMensaje: false,
            showNoVoy: false
        };

        this.handleCloseNoVoy = this.handleCloseNoVoy.bind(this);
        this.handleCloseTexto = this.handleCloseTexto.bind(this);
        this.clickYaConfirme = this.clickYaConfirme.bind(this);
        this.clickNoVoy = this.clickNoVoy.bind(this);
        this.clickFaltaConfirmar = this.clickFaltaConfirmar.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleErrorClose = this.handleErrorClose.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.confirmAssistance = this.confirmAssistance.bind(this);
        this.confirmNoAssistance = this.confirmNoAssistance.bind(this);
        this.generateErrorWpMessage = this.generateErrorWpMessage.bind(this);
        this.parseNameToMessage = this.parseNameToMessage.bind(this);
        this.onInvitadoChange = this.onInvitadoChange.bind(this);
        this.generateConfirmationMessage = this.generateConfirmationMessage.bind(this);
        this.generateNoConfirmationMessage = this.generateNoConfirmationMessage.bind(this);
        this.generateRestriccionesMessage = this.generateRestriccionesMessage.bind(this);
    }

    generateConfirmationMessage() {
        let message = 'Hola%20te%20escribo%20para%20confirmar%20la%20asistencia%20de:';
        /*let invitadosConfirmados = this.state.invitadosConfirmados;
        Object.keys(invitadosConfirmados).map(function (keyName, keyIndex) {
            let invitado = invitadosConfirmados[keyName];
            if (invitado.confirmado) {
                message = message + ' ' + invitado.nombre.replace(' ', '%20');
                if (invitado.celiaco || invitado.diabetico || invitado.vegano) {
                    if (invitado.celiaco) message = message + '(celiaco/a)';
                    if (invitado.diabetico) message = message + '(diabetico/a)';
                    if (invitado.vegano) message = message + '(vegano/a)';
                }
                message = message + ','
            }
            return message;
        })
        message = message.substring(0, message.length - 1);*/
        return message;
    }

    generateRestriccionesMessage() {
        let message = 'Hola,%20te%20queria%20avisar%20que%20tengo%20la%20siguiente%20restricción%20alimenticia: ';
        return message;
    }

    generateNoConfirmationMessage() {
        let message = 'Hola%20lamentablemente%20no%20vamos%20a%20poder%20asistir,%20nombres:';
        return message;
    }

    onInvitadoChange(invitado) {
        let newInvitadosConfirmados = this.state.invitadosConfirmados;
        newInvitadosConfirmados[invitado.nombre.toUpperCase()] = invitado;
    }

    generateErrorWpMessage() {
        this.handleErrorClose();
        this.openInNewTab('Hola%20no%20pude%20confirmar%20asistencia%20para%20' + this.parseNameToMessage());
    }

    parseNameToMessage() {
        let array = this.state.nombre.split(' ');
        let parsedName = array.join('%20');
        return parsedName;
    }

    confirmAssistance(event) {
        if (!this.state.mostrarMensaje) this.openInNewTab(this.generateRestriccionesMessage());
        else this.openInNewTab(this.generateConfirmationMessage());
        this.handleClose(event);
    }

    confirmNoAssistance(event) {
        this.openInNewTab(this.generateNoConfirmationMessage());
        this.handleClose(event);
    }

    openInNewTab(message) {
        window.open(this.mensajeWp + message, '_blank', 'noopener,noreferrer');
    };


    handleClose(event) {
        this.setState({ showModal: false });
    }

    handleCloseNoVoy(event) {
        this.setState({ showNoVoy: false });
    }

    handleCloseTexto(event) {
        this.setState({ showModalTexto: false });
    }

    handleErrorClose(event) {
        this.setState({ showErrorModal: false });
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        if (this.state.nombre.trim() === '') {
            this.setState({ showErrorMessage: true });
        } else {
            this.setState({ showErrorMessage: false });
            if (this.list.has(this.state.nombre.toUpperCase())) {
                this.setState({ invitados: this.list.get(this.state.nombre.toUpperCase()) });
                this.setState({ showModal: true });
            } else {
                this.setState({ showErrorModal: true });
            }
        }
    }

    clickYaConfirme(event) {
        event.stopPropagation();
        event.preventDefault();
        this.setState({ mostrarMensaje: false, showModalTexto: true });

    }

    clickNoVoy(event) {
        event.stopPropagation();
        event.preventDefault();
        this.setState({ showNoVoy: true });
    }

    clickFaltaConfirmar(event) {
        event.stopPropagation();
        event.preventDefault();
        this.setState({ mostrarMensaje: true, showModalTexto: true });
    }
    // bg-slate-100/50
    render() {
        return (
            <div className="page flex h-100 items-center justify-end flex-1">
                <div className="w-100 flex flex-col text-center m-auto p-10 pt-4">
                    <div className="pt-2">
                        <div className="m-auto aspect-square w-[25vw] circle bg-icono">
                            <InlineIcon className="w-[60%] h-100 d-block m-auto text-4xl text-white" icon="mdi:identification-card-outline"></InlineIcon>
                        </div>
                    </div>
                    <div className="leading-none w-100 whitespace-pre-wrap text-rouge h-1/4 text-center text-white text-[3rem]">
                        <span className="text-[5rem] font-bold">·</span>R.S.V.P.<span className="text-[5rem] font-bold">·</span>
                    </div>
                    <div className="flex flex-column items-center justify-end mt-4">
                        <div className="m-auto py-2">
                            <AwesomeButton type="primary" onPress={(e) => this.clickYaConfirme(e)} className="boton">Ya confirmé</AwesomeButton>
                        </div>
                        <div className="m-auto py-2">
                            <AwesomeButton type="primary" onPress={(e) => this.clickNoVoy(e)} className="boton">No voy</AwesomeButton>
                        </div>
                        <div className="m-auto py-2">
                            <AwesomeButton type="primary" onPress={(e) => this.clickFaltaConfirmar(e)} className="boton">Me falta confirmar</AwesomeButton>
                        </div>
                    </div>
                    <Modal show={this.state.showModalTexto} onHide={this.handleCloseTexto} centered>
                        <Modal.Header closeButton>
                            <Modal.Title>Restricciones Alimenticias</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <label>Tenes alguna restricción alimenticia?</label>
                            <ul className="ul-restricciones">
                                <li>Celíaco</li>
                                <li>Diabético</li>
                                <li>Vegetariano</li>
                                <li>Vegano</li>
                            </ul>
                            <label>No te olvides avisarnos!</label>
                            <label>{this.state.mostrarMensaje && "Confirma en el siguiente botón"}</label>
                        </Modal.Body>
                        <Modal.Footer>
                            <AwesomeButtonSocial type="whatsapp" onPress={() => this.confirmAssistance(this)}>Confirmar</AwesomeButtonSocial>
                        </Modal.Footer>
                    </Modal>
                    <Modal show={this.state.showNoVoy} onHide={this.handleCloseNoVoy} centered>
                        <Modal.Header closeButton>
                        </Modal.Header>
                        <Modal.Body>
                            <label>Te recomendamos confirmar la NO asistencia con el siguiente botón</label>
                        </Modal.Body>
                        <Modal.Footer>
                            <AwesomeButtonSocial type="whatsapp" onPress={() => this.confirmNoAssistance(this)}>Confirmar</AwesomeButtonSocial>
                        </Modal.Footer>
                    </Modal>
                </div>
            </div>
        )
    }
}
