import React, { Component } from "react";
import { InlineIcon } from "@iconify/react";

class Regalo extends Component {
    render() {
        return (
            <div className="fondo-pagina page h-100 flex flex-col pb-4">
                <div className="pt-4">
                    <div className="m-auto aspect-square w-[25vw] circle bg-icono">
                        <InlineIcon className="w-[60%] h-100 d-block m-auto text-4xl text-white" icon="mdi:gift-outline"></InlineIcon>
                    </div>
                </div>
                <div className="leading-none w-100 whitespace-pre-wrap text-rouge h-1/4 text-center text-white text-[3rem]">
                    Querés hacernos un regalo?
                </div>
                <div className="my-3 leading-none w-100 whitespace-pre-wrap font-subtitulo h-1/4 text-center text-white text-[1.5rem]">
                    Tu presencia es el mejor regalo, pero si aún así querés hacernos un obsequio podés hacerlo aquí
                </div>
                <div className="leading-none w-100 whitespace-pre-wrap font-subtitulo h-1/4 text-center text-white text-[1.2rem]">
                    <ul>
                        <li className="mb-2">USD BBVA 22048731 Matías Rodríguez</li>
                        <li>UYU BROU 001288752-00001 Sofía Sosa</li>
                    </ul>

                </div>
            </div>
        );
    }
}

export default Regalo;