import React, { Component } from "react";
import { InlineIcon } from "@iconify/react";
import { Parallax } from "react-parallax";
import background from "../images/wedshoots.png";
import { AwesomeButton } from "react-awesome-button";

class Wedshoots extends Component {

    constructor(props) {
        super(props);
        this.clickAlbum = this.clickAlbum.bind(this);
    }

    clickAlbum() {
        window.location.href = 'http://www.wedshoots.com/download?albumId=ESda857e26';
    }

    render() {
        return (
            <div className="fondo-pagina page h-100 flex flex-col pb-4 bg-white text-green">
                <div className="pt-4">
                    <div style={{
                        backgroundImage: `url(${background})`
                        , backgroundPosition: 'center',
                        backgroundSize: "cover",
                        borderRadius: "20px"
                    }} className="border-green m-auto aspect-square w-[25vw] ">

                    </div>
                </div>
                <div className="leading-none w-100 whitespace-pre-wrap text-rouge h-1/4 text-center  text-[3rem]">
                    <span className="text-[5rem] font-bold">·</span>Wedshoots<span className="text-[5rem] font-bold">·</span>
                </div>
                <div className="leading-none w-100 whitespace-pre-wrap font-subtitulo h-1/4 text-center text-[2rem]">
                    Te dejamos el álbum para compartir tus fotos del casamiento con nosotros
                </div>
                <div className="flex flex-column items-center justify-end mt-4">
                    <div className="m-auto py-2">
                        <AwesomeButton type="primary" href="http://www.wedshoots.com/download?albumId=ESda857e26" className="boton">Álbum</AwesomeButton>
                    </div>
                </div>
            </div>
        );
    }
}

export default Wedshoots;