import React from 'react';
import DateTimeDisplay from './DateTimeDisplay';
import { useCountdown } from '../hooks/useCountdown';
import background from "../images/fireworks.gif";

const ShowCounter = ({ days, hours, minutes, seconds }) => {
    return (
        <div>
            <div className='w-100 d-flex countdown justify-between'>
                <div className='w-100 flex flex-column'>
                    <div className='contador mb-2'>
                        <div>
                            <p>{days}</p>
                            <span>Días</span>
                        </div>
                    </div>
                    <div className='contador'>
                        <div>
                            <p>{hours}</p>
                            <span>Horas</span>
                        </div>
                    </div>
                </div>
                <div className='w-100 flex flex-column'>
                    <div className='contador mb-2'>
                        <div>
                            <p>{minutes}</p>
                            <span>Minutos</span>
                        </div>
                    </div>
                    <div className='contador'>
                        <div>
                            <p>{seconds}</p>
                            <span>Segundos</span>
                        </div>
                    </div>
                </div>

            </div>
        </div>


    );
};

const Termino = () => {
    return (
        <div style={{
            backgroundImage: `url(${background})`
            , backgroundPosition: 'center',
            backgroundSize: "cover",
            aspectRatio: '1/1',
            maxWidth: "300px",
            margin: "auto"
        }}></div>
    );
};
/*

<div className="show-counter">
                <div className="countdown-link">
                    <DateTimeDisplay value={days} type={'Dias'} isDanger={days <= 3} />
                    <p>:</p>
                    <DateTimeDisplay value={hours} type={'Horas'} isDanger={false} />
                    <p>:</p>
                    <DateTimeDisplay value={minutes} type={'Min'} isDanger={false} />
                    <p>:</p>
                    <DateTimeDisplay value={seconds} type={'Seg'} isDanger={false} />
                </div>
            </div>
            */
const CountdownTimer = ({ targetDate }) => {
    const [days, hours, minutes, seconds] = useCountdown(targetDate);

    if (days + hours + minutes + seconds <= 0) {
        return <Termino />;
    } else {
        return (
            <ShowCounter
                days={days}
                hours={hours}
                minutes={minutes}
                seconds={seconds}
            />
        );
    }
};

export default CountdownTimer;
