import React from "react";
import { Parallax } from "react-parallax";
import background from "../images/imagenFinal.jpg";

export const ImagenFinal = () => {
    return (
        <div style={{
            backgroundImage: `url(${background})`
            , backgroundPosition: 'center',
            backgroundSize: "cover",
            height: '40vh'
        }} className="page text-rouge d-flex justify-center items-center text-center text-white ">
            <p className="pb-14 self-end text-[35px]">Los esperamos</p>
        </div>
    )
}
